/* ==================================================
   Footer
================================================== */
footer {
  position: relative;
  background: $blanco;
  padding: 16px 0;
}

#social-footer ul {
  margin: 0;
  padding: 0;
  text-align: center;

  li {
    cursor: pointer;
    display: inline-block;
    list-style: none;
    float: none;
    margin: 0 0 0 -5px;

    &:hover a {
      background-color: $gris-anubis;
    }

    &:hover a i {
      color: $blanco;
      opacity: 1;
    }

    // &:hover a img {
    //     // color: $blanco;
    //     opacity: 1;
    // }
    a {
      background-color: transparent;
      -webkit-border-radius: 2px 2px 2px 2px;
      -moz-border-radius: 2px 2px 2px 2px;
      border-radius: 2px 2px 2px 2px;
      display: block;
      height: 45px;
      width: 70px;
      -webkit-transition: background 0.35s ease-in-out 0s;
      -moz-transition: background 0.35s ease-in-out 0s;
      -o-transition: background 0.35s ease-in-out 0s;
      transition: background 0.35s ease-in-out 0s;

      i {
        color: $negro;
        display: inline-block;
        font-size: 32px;
        line-height: 45px;
        margin: 0;
        opacity: 0.5;
        filter: alpha(opacity = 30);
        -webkit-transition: color 0.15s ease-in-out 0s;
        -moz-transition: color 0.15s ease-in-out 0s;
        -o-transition: color 0.15s ease-in-out 0s;
        transition: color 0.15s ease-in-out 0s;
      }

      img {
        display: inline-block;
        margin: -3px 0 0;
        opacity: 1;
        filter: alpha(opacity = 100);
      }
    }
  }
}

