@mixin transition($transition-property, $transition-time, $method) {
  -webkit-transition: $transition-property $transition-time $method;
  -moz-transition: $transition-property $transition-time $method;
  -ms-transition: $transition-property $transition-time $method;
  -o-transition: $transition-property $transition-time $method;
  transition: $transition-property $transition-time $method;
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min--moz-device-pixel-ratio: 1.5), only screen and (-o-min-device-pixel-ratio: 3 / 2), only screen and (min-device-pixel-ratio: 1.5), only screen and (min-resolution: 192dpi) {
  /* Style Rules */
}

$negro: black;
$blanco: #f8f8f8;
$perla: #E1E1E1;
$marron: #5b5852;
$verde: #54b589;
$salmon: #F97E76;
$gris: #7c7b79;
$gris-dot: rgb(187, 187, 187);
$gris-anubis: #323A45;

$times: TimesNewRoman, "Times New Roman", Times, Baskerville, Georgia, serif;
$open: "Open Sans", sans-serif;

$museo: "Museo Slab";
$avant: "ITCAVANTGARDEGOTHICLTMEDIUM";

/* ==================================================
   Responsiveness
================================================== */

// BOOTSTRAP LG //

@media (min-width: 992px) and (max-width: 1199px) {
  ///////////////////
  #slider {
    margin-top: 0px;
  }

  header {
    height: 134px;

    // #logo{
    // 	height: 55px;
    // 	margin-top: 5px;
    // 	margin-bottom: 33px;
    // 	a{
    // 		line-height: 75px;
    // 	}
    // 	img{
    // 		height: 55px;
    // 	}
    // }

  }

  #menu {
    display: block;
  }

  #navbar {
    width: 100%;
  }

  nav#menu {}

  nav#menu #menu-nav li {
    display: inline-block;
    list-style: none;
    float: none;
    padding: 0 17px;

    a {
      font-size: 16px;
      line-height: 35px;
    }
  }

  #mobile-nav {
    display: none;
    float: none;
  }

  footer {
    position: relative;
    background: $blanco;
    padding: 8px 0 6px;
  }

  #social-footer ul li a {
    height: 36px;
    width: 66px;

    i {
      font-size: 24px;
      line-height: 36px;
      margin: 0;
    }

    img {
      height: 35px;
      margin-top: -2px;
    }
  }

  /* ===================================
     DropDown
  =================================== */
  .works_title {
    h1 {
      font-family: Museo Slab;
      font-size: 23px;
      color: $blanco;
      font-weight: lighter;
      padding: 0px 0 0px 0;
      margin: 30px 0 0px 0;
    }
  }

  #dropdown_hidden.dropmenu {
    display: none;
  }

  #mob-navbar_hidden {
    display: none;
  }

  #navbar_hidden {
    display: inline-block;
  }
}

