  }

  #dropdown_hidden.dropmenu {
    display: inline-block;
  }

  #mob-navbar_hidden {
    display: none;
  }

  #navbar_hidden {
    display: none;
  }

  #portfolio-projects {
    margin-left: -20px;
    margin-right: -20px;
    margin-bottom: 0px;
  }

  #projects li {
    list-style: none;
    margin-bottom: 15px !important;

    // padding-bottom: 15px;

  }

  .dropmenu-active {
    margin: 0;
    padding: 0;
    -webkit-border-radius: 0 2px 2px 0px !important;
    -moz-border-radius: 0 2px 2px 0px !important;
    border-radius: 0 2px 2px 0px !important;
  }
